// Changed name to caver.js 2018-10

var errors = require('../../../caver-core-helpers').errors;
/**
 * xhr2
 * xhr2 library w3c   XMLHttpRequest  .
 */
var XHR2 = require('xhr2-cookies').XMLHttpRequest

/**
 * HttpProvider should be used to send rpc calls over http
 */
/**
 * @param       {string} host
 * @param       {object} options
 * @constructor
 * host options    instance  .
 * construct   connecetd  (   ) false.
 * 'host', 'timeout', 'headers', 'connected' property .
 */
var HttpProvider = function HttpProvider(host, options) {
    options = options || {};
    /**
     * host    localhost 8545  host .
     */
    this.host = host || 'http://localhost:8545';
    this.timeout = options.timeout || 0;
    this.headers = options.headers;
    this.connected = false;
};

/**
 * _prepareRequest
 *   request instance    .
 * 1) XHR2  xhr request instance ,
 * 2) 'POST'  , constructor  host host ,
 *    async send  true .
 * 3) json rpc   , requestHeader Content-type 'application/json' .
 * 4) instance timeout   &&   1     ,
 *      0 timeout .
 * 5) instance headers    setRequestHeader header name header value .
 * 6) 1~5    request instance .
 */
HttpProvider.prototype._prepareRequest = function(){
    var request = new XHR2();

    request.open('POST', this.host, true);
    request.setRequestHeader('Content-Type','application/json');
    request.timeout = this.timeout && this.timeout !== 1 ? this.timeout : 0;

    if(this.headers) {
        this.headers.forEach(function(header) {
            request.setRequestHeader(header.name, header.value);
        });
    }

    return request;
};

/**
 * Should be used to make async request
 *
 * @method send
 * @param {Object} payload
 * @param {Function} callback triggered on end with (err, result)
 */

/**
 *  rpc request  .
 *      .
 * 1) onreadystatechange - request   response    
 * 2) ontimeout - timeout     
 * 3) send  try, catch -
 */
HttpProvider.prototype.send = function (payload, callback) {
  var _this = this;

  _this.send(payload, callback)
};

HttpProvider.prototype.send = function(payload, callback) {
  var _this = this;
  var request = this._prepareRequest();

  request.onreadystatechange = function() {
      /**
       * readystate 
       * 0: UNSENT - client created 
       * 1: OPENED - request open  
       * 2: HEADERS_RECEIVED - send  headers status available 
       * 3: LOADING - downloading
       * 4: DONE - request  response  .
       * @todo request.timeout !== 1 
       * @body constructor  timeout 1   ? (request.timeout !== 1)
       */
      if (request.readyState === 4 && request.timeout !== 1) {
          /**
           * response    request.responseText  .
           */
          var result = request.responseText;
          /**
           * error  null  .
           */
          var error = null;

          /**
           *  responseText(JSON) parse.
           *    InvalidResponse  .
           */
          try {
              result = JSON.parse(result);
          } catch(e) {
              error = errors.InvalidResponse(request.responseText);
          }

          /**
           * HttpProvider instnace connected property true .
           */
          _this.connected = true;
          /**
           * send     (.send  parameter ) .
           */
          callback(error, result);
      }
  };

  /**
   * ontimeout
   * timeout  HttpProvider instance connected false 
   * ConnectionTimeout error message  .
   */
  request.ontimeout = function() {
      _this.connected = false;
      callback(errors.ConnectionTimeout(this.timeout));
  };

  try {
      request.send(JSON.stringify(payload));
  } catch(error) {
      this.connected = false;
      callback(errors.InvalidConnection(this.host));
  }
}

module.exports = HttpProvider;
