/**
 * jsonrpc , messageId   0  .
 */
// Initialize Jsonrpc as a simple object with utility functions.
var Jsonrpc = {
    messageId: 0
};

/**
 * Should be called to valid json create payload object
 *
 * @method toPayload
 * @param {Function} method of jsonrpc call, required
 * @param {Array} params, an array of method params, optional
 * @returns {Object} valid jsonrpc payload object
 */
/**
 * jsonrpc    
 * method params  messageId   
 * jsonrpc    .
 * jsonrpc request object 'method' property    ,
 * method    .
 *  messageId    
 * jsonrpc  2.0  
 */
Jsonrpc.toPayload = function (method, params) {
    /**
     * parameter 'method'   
     */
    if (!method) {
        throw new Error('JSONRPC method should be specified for params: "'+ JSON.stringify(params) +'"!');
    }

    /**
     * message jsonrpc    messageId  .
     */
    Jsonrpc.messageId++;

    /**
     * jsonrpc   reqeust object .
     */
    return {
        jsonrpc: '2.0',
        id: Jsonrpc.messageId,
        method: method,
        params: params || []
    };
};

/**
 * jsonrpc   response object  .
 */
Jsonrpc.isValidResponse = function (response) {
    /**
     * response array   valid ,
     * array native  'every'      valid  false .
     * array ,  response  validation .
     */
    return Array.isArray(response) ? response.every(validateSingleMessage) : validateSingleMessage(response);

    /**
     * valid jsonrpc response,
     * i)  message truthy 
     * ii) message.error  ,
     * iii) message.jsonrpc '2.0'    ,
     * iv) message.id 'number' 'string'   ,
     * v) message.result   . (undefined  .)
     */
    function validateSingleMessage(message){
        /**
         * !!  null   true or false    
         * https://stackoverflow.com/questions/784929/what-is-the-not-not-operator-in-javascript
         */
        return !!message &&
        !message.error &&
        message.jsonrpc === '2.0' &&
        (typeof message.id === 'number' || typeof message.id === 'string') &&
        message.result !== undefined; // only undefined is not valid json object
    }
};

/**
 * Batch Call   payload   
 * toBatchPayload  , message  ,    
 *   toPayload       .
 * ex) [{ method: 'A', params: 1 }, { method: 'B', params: 4 }, { method: 'C', params: 2 }]
 *  messages   , toBatchPayload     ,
 * [
 *  { jsonrpc: '2.0', id: 0, method: 'A', params: 1 },
 *  { jsonrpc: '2.0', id: 1, method: 'B', params: 4 },
 *  { jsonrpc: '2.0', id: 2, method: 'C', params: 2 },
 * ]
 *   .
 */
Jsonrpc.toBatchPayload = function (messages) {
    return messages.map(function (message) {
        return Jsonrpc.toPayload(message.method, message.params);
    });
};

module.exports = Jsonrpc;
