const Subscription = require('./subscription.js')

/**
 * Subscriptions constructor
 * Subscriptions instance  constructor, options parameter 
 * name, type, subscriptions options   .
 * requestManager null  . (  setRequestManager   .)
 */

function Subscriptions(options) {
  this.name = options.name
  this.type = options.type
  this.subscriptions = options.subscriptions || {}
  this.requestManager = null
};

/**
 * setRequestManager
 * requestManager Subscriptions instance .
 */
Subscriptions.prototype.setRequestManager = function (requestManager) {
  this.requestManager = requestManager
};

/**
 * attachToObject
 * buildCall     parameter  object attach .
 */
Subscriptions.prototype.attachToObject = function (obj) {
    var func = this.buildCall()
    /**
     * Subscriptions instantiate   options  name 
     * new CaverSubscriptions({
     *       name: 'subscribe',
     *      type: 'eth',
     *      subscriptions: {
     *          'newBlockHeaders': {
     *              subscriptionName: 'newHeads',
     *              params: 0,
     *              outputFormatter: formatters.outputBlockFormatter
     *          },
     *          'pendingTransactions': {
     *              params: 0,
     *              outputFormatter: formatters.outputTransactionFormatter
     *          }
     *      }
     *  })
         , name 'xxx.subscribe'    split 
       parameter  obj   .
        obj.xxx.subscribe    .
       ) Javascript object reference type object  property 
              reference .
     */
    var name = this.name.split('.');
    if (name.length > 1) {
        obj[name[0]] = obj[name[0]] || {};
        obj[name[0]][name[1]] = func;
    } else {
        obj[name[0]] = func;
    }
};

/**
 * buildCall
 * subscription instance    .
 * )    Javascript    .
 *   : https://hackernoon.com/effective-functional-javascript-first-class-and-higher-order-functions-713fde8df50a
 */
Subscriptions.prototype.buildCall = function() {
    var _this = this;

    return function(){
        /**
         * Subscriptions instance   subscriptions object ,
         * 
         *            subscriptions: {
         *               'newBlockHeaders': {
         *                   subscriptionName: 'newHeads',
         *                   params: 0,
         *                   outputFormatter: formatters.outputBlockFormatter
         *              },
         *               'pendingTransactions': {
         *                   params: 0,
         *                   outputFormatter: formatters.outputTransactionFormatter
         *              }
         *   , buildCall()   argument   ,
         * subscriptions property    console.warn  .
         * (   subscribe .)
         */
        if(!_this.subscriptions[arguments[0]]) {
            console.warn('Subscription '+ JSON.stringify(arguments[0]) +' doesn\'t exist. Subscribing anyway.');
        }

        /**
         * subscription.js    Subscription instantiate .
         * subscription, requestManager, type .
         *   type 'eth'  string.
         * ) buildCall  ,    arguments[0]  ,
         *    Subscription instantiate  .
         * ( console.warn .)
         */
        var subscription = new Subscription({
            subscription: _this.subscriptions[arguments[0]],
            requestManager: _this.requestManager,
            type: _this.type
        });

        /**
         * subscription.js  subscribe  , buildCall  
         * argument  (arguments)  .
         */
        return subscription.subscribe.apply(subscription, arguments);
    };
};


module.exports = {
    subscriptions: Subscriptions,
    subscription: Subscription
};
