var _ = require('underscore');
var errors = require('../../caver-core-helpers').errors;
var EventEmitter = require('eventemitter3');


function Subscription(options) {
  /**
   * EventEmitter constructor   . (eventemitter3  )
   * function EventEmitter() {
   *  this._events = new Events();
   *  this._eventsCount = 0;
   * }
   * EventEmitter.call(this)  ,  Subscription  (this) 
   * this._event = new Events();
   * this._eventsCount = 0;   .
   *    , var sub = new Subscription();  Subscription instance   ,
   * sub._event, sub._eventsCount    .
   * : Object.create()    
   * : https://developer.mozilla.org/ko/docs/Web/JavaScript/Reference/Global_Objects/Object/create
   */
    EventEmitter.call(this);

    /**
     * Subscription id, callback, arguments, _reconnectIntervalId null .
     */
    this.id = null;
    this.callback = null;
    this.arguments = null;
    this._reconnectIntervalId = null;

    /**
     * )  Subscriptions   instantiate .
     *  new Subscriptions({
     *       name: 'subscribe',
     *       type: 'eth',
     *       subscriptions: {
     *           'newBlockHeaders': {
     *               subscriptionName: 'newHeads', // replace subscription with this name
     *               params: 0,
     *               outputFormatter: formatters.outputBlockFormatter
     *           }
     *      }
     *   })
     */

    this.options = {
        subscription: options.subscription,
        type: options.type,
        requestManager: options.requestManager
    };
}

// INHERIT
/**
 * Subscription prototype EventEmitter.prototype , EventEmitter.prototype 
 *      .
 */
Subscription.prototype = Object.create(EventEmitter.prototype);
/**
 *  Subscription.prototype  EventEmitter.prototype ,
 * constructor Subscription constructor , EventEmitter constructor  ,
 * constructor Subscription .
 */
Subscription.prototype.constructor = Subscription;


/**
 * Should be used to extract callback from array of arguments. Modifies input param
 *
 * @method extractCallback
 * @param {Array} arguments
 * @return {Function|Null} callback, if exists
 */

/**
 * _extractCallback
 * parameter  args    callback      ,
 *  ,    callback    .
 * underscore isFunction     'function'  ,
 *  pop   () .
 * ) _.isFunction(arg) typeof(arg) == 'function'  . (underscore    .)
 */
Subscription.prototype._extractCallback = function (args) {
    if (_.isFunction(args[args.length - 1])) {
        return args.pop(); // modify the args array!
    }
};

/**
 * Should be called to check if the number of arguments is correct
 *
 * @method validateArgs
 * @param {Array} arguments
 * @throws {Error} if it is not
 */

/**
 * _validateArgs
 * parameter  args validation(arguments  subscription params )
 *   .
 * , this.options subscription  
 * subscription {}  object, subscription.params 0    .
 */
Subscription.prototype._validateArgs = function (args) {
    var subscription = this.options.subscription;

    if(!subscription)
        subscription = {};

    if(!subscription.params)
        subscription.params = 0;

    if (args.length !== subscription.params) {
        throw errors.InvalidNumberOfParams(args.length, subscription.params + 1, args[0]);
    }
};

/**
 * Should be called to format input args of method
 *
 * @method formatInput
 * @param {Array}
 * @return {Array}
 */

/**
 * _formatInput
 * inputFormatter  subscription  ,(array   .)
 * inputFormatter    parameter  args format.
 *  inputFormatter  subscription   , formatInput 
 * formatter  ,   args  return .
 */
Subscription.prototype._formatInput = function (args) {
    var subscription = this.options.subscription;

    /**
     * subscription      args   
     */
    if (!subscription) {
        return args;
    }

    /**
     * inputFormatter    args   
     */
    if (!subscription.inputFormatter) {
        return args;
    }

    /**
     * inputFormatter(array)   ,    formatter
     * args(array)  format .
     */
    var formattedArgs = subscription.inputFormatter.map(function (formatter, index) {
        return formatter ? formatter(args[index]) : args[index];
    });

    /**
     *  args   format   format args . (formattedArgs)
     */
    return formattedArgs;
};

/**
 * Should be called to format output(result) of method
 *
 * @method formatOutput
 * @param {Object}
 * @return {Object}
 */

/**
 * _formatOutput
 * i) parameter result   truthy ,
 * ii) subscription outputFormatter  ,
 * outputFormatter  result format.
 */
Subscription.prototype._formatOutput = function (result) {
    var subscription = this.options.subscription;

    return (subscription && subscription.outputFormatter && result) ? subscription.outputFormatter(result) : result;
};

/**
 * Should create payload from given input args
 *
 * @method toPayload
 * @param {Array} args
 * @return {Object}
 */

/**
 * _toPayload
 *
 */
Subscription.prototype._toPayload = function (args) {
    /**
     * params   ,  params   ,  
     *     .
     * [this.subscriptionMethod, ...this.arguments]
     */
    var params = [];
    /**
     * _extractCallback   args  index  callback  .
     *    args   ()   .
     */
    this.callback = this._extractCallback(args);

    /**
     * this.subscriptionMethod   , args      ,
     * this.subscriptionMethod .
     */
    if (!this.subscriptionMethod) {
        this.subscriptionMethod = args.shift();

        // replace subscription with given name
        /**
         * Subscription instance     options
         * subscription.subscriptionName  ,
         * this.subscriptionMethod   .
         * subscriptionName   .
         * 'pendingTransactions', 'newBlockHeaders', 'syncing', 'logs'
         */
        if (this.options.subscription.subscriptionName) {
            this.subscriptionMethod = this.options.subscription.subscriptionName;
        }
    }

    /**
     * Subscription instance this.arguments    (null )
     * 1) _toPayload parameter  args formatInput  format  this.arguments ,
     * 2)    validation  .
     * 3)   , params   args   .
     * , args     1) 2)    ,
     * this.arguments format args   ,
     * _validateArgs  subscription.params this.arguments     .
     */
    if (!this.arguments) {
        this.arguments = this._formatInput(args);
        this._validateArgs(this.arguments);
        args = []; // make empty after validation

    }

    // re-add subscriptionName
    /**
     *    'params' this.subscriptionMethod .
     * params == [this.subscriptionMethod]
     */
    params.push(this.subscriptionMethod);
    /**
     * 'params' this.arguments(format args)  concat .
     * params == [this.subscriptionMethod, ...this.arguments]
     * ) ...this.arguments ES6 spread operator.    
     *    operator.
     *  : https://developer.mozilla.org/ko/docs/Web/JavaScript/Reference/Operators/Spread_operator
     */
    params = params.concat(this.arguments);

    /**
     * this.arguments     args    ,    .
     * this.arguments      subscription  instantiate  ,
     * _toPayload  (Subscription.prototype.subscribe )   _extractCallback  args   ()   .
     * ,
     * i)  subscription instantiate ,
     * ii)  toPayload    parameter  args callback       
     *     throw .
     */
    if (args.length) {
        throw new Error('Only a callback is allowed as parameter on an already instantiated subscription.');
    }

    /**
     * this.options.type    '_subscribe' string  method ,
     * params params  .
     */
    return {
        method: this.options.type + '_subscribe',
        params: params
    };
};

/**
 * Unsubscribes and clears callbacks
 *
 * @method unsubscribe
 * @return {Object}
 */
/**
 * unsubscribe
 * 1) requestManager  removeSubscription  subscription ,
 * rpc unsubscribe  requestManager removeSubscription  .
 * 2) subscription id  this.id null  
 * 3) eventemitter3   removeAllListeners  
 *  instance  eventListener  .
 * 4) Subscription.prototype.subscribe  setInterval   
 * clearInterval  .
 */
Subscription.prototype.unsubscribe = function(callback) {
    this.options.requestManager.removeSubscription(this.id, callback);
    this.id = null;
    this.removeAllListeners();
    clearInterval(this._reconnectIntervalId);
};

/**
 * Subscribes and watches for changes
 *
 * @method subscribe
 * @param {String} subscription the subscription
 * @param {Object} options the options object with address topics and fromBlock
 * @return {Object}
 */
Subscription.prototype.subscribe = function() {
    var _this = this;
    /**
     * arguments  subscribe  parameter    ,
     * subscribe(arg1, arg2, arg3, arg4)    
     *  arg1, arg2, arg3, arg4   arguments  .
     * , parameter     parameter     
     * .  arguments ''  ''    
     *   .   Array.prototype.slice.call(arguments)  
     *  ' ' . args  ' '    .
     */
    var args = Array.prototype.slice.call(arguments);
    /**
     * args payload .
     *   payload
     * {
     *  method: ...,
     *  params: ...,
     * }
     *    .
     */
    var payload = this._toPayload(args);

    /**
     * payload    Subscription instance .
     */
    if(!payload) {
        return this;
    }

    /**
     * requestManager provider      .
     * emit eventemitter3 prototype method.
     */
    if(!this.options.requestManager.provider) {
        var err1 = new Error('No provider set.');
        this.callback(err1, null, this);
        this.emit('error', err1);
        return this;
    }

    // throw error, if provider doesnt support subscriptions
    /**
     * requestManager provider on   (subscription   )
     *  .
     * emit eventemitter3 prototype method.
     */
    if(!this.options.requestManager.provider.on) {
        var err2 = new Error('The current provider doesn\'t support subscriptions: '+ this.options.requestManager.provider.constructor.name);
        this.callback(err2, null, this);
        this.emit('error', err2);
        return this;
    }

    // if id is there unsubscribe first
    /**
     *  Subscription instance id   ,  unsubscribe.
     */
    if (this.id) {
        this.unsubscribe();
    }

    // store the params in the options object
    /**
     * payload.params    .
     * [type, options, callback]
     * type 'pendingTransactions', 'newBlockHeaders', 'syncing', 'logs'  string ,
     * options
     * {
     *   address: '0x123456..',
     *   topics: ['0x12345...']
     * }    object.
     *  callback   subscribe    callback.
     *  type, options, callback  payload.params[0], payload.params[1], payload.params[2]  .
     */
    this.options.params = payload.params[1];

    // get past logs, if fromBlock is available
    if (payload.params[0] === 'logs'
      && _.isObject(payload.params[1])
      && payload.params[1].hasOwnProperty('fromBlock')
      && isFinite(payload.params[1].fromBlock)
    ) {
        // send the subscription request
        this.options.requestManager.send({
            method: 'klay_getLogs',
            params: [payload.params[1]]
        }, function (err, logs) {
            if(!err) {
                logs.forEach(function(log){
                    var output = _this._formatOutput(log);
                    _this.callback(null, output, _this);
                    _this.emit('data', output);
                });

                // TODO subscribe here? after the past logs?

            } else {
                _this.callback(err, null, _this);
                _this.emit('error', err);
            }
        });
    }

    // create subscription
    // TODO move to separate function? so that past logs can go first?

    /**
     * ?TODO: payload.params[0] 'logs'   fromBlock option  
     * delete   .
     */
    if (typeof payload.params[1] === 'object')
        delete payload.params[1].fromBlock;
    /**
     * requestManager send     payload ,
     *    .
     *        .
     * case 1) 
     * case 2)  -   
     * case 3)  -   
     */
    this.options.requestManager.send(payload, function (err, result) {
      /**
       * payload requestManager.send    
       * case 1)  -   result   ( result subscription id .)
       * - _this.id(subscription id) result  .
       * - addSubscription subscription id, subscription type, type(ex: eth)
       * - ,    .
       */
        if (!err && result) {
            _this.id = result;

            // call callback on notifications
            _this.options.requestManager.addSubscription(_this.id, payload.params[0], _this.options.type, function(err, result) {

                /**
                 * case 1-1)  - addSubscription      
                 * 1) result  array ,
                 * 2) array  result    _this._formatOutput  format.
                 * 3) subscriptionHandler   , handler ,
                 *    , eventemitter _this.emit    output emit.
                 * 4) (optional) _this.callback  ,  .
                 */
                if (!err) {
                    if (!_.isArray(result)) {
                        result = [result];
                    }

                    result.forEach(function(resultItem) {
                        var output = _this._formatOutput(resultItem);

                        if (_.isFunction(_this.options.subscription.subscriptionHandler)) {
                            return _this.options.subscription.subscriptionHandler.call(_this, output);
                        } else {
                            _this.emit('data', output);
                        }

                        // call the callback, last so that unsubscribe there won't affect the emit above
                        if (_.isFunction(_this.callback)) {
                            _this.callback(null, output, _this);
                        }
                    });
                } else {
                    /**
                     * case 1-2)  - addSubscription      
                     * 1) requestManager removeSubscription  subscription .
                     * 2) connection fail  setInterval  500ms  
                     *    provider.reconnect() . reconnect , provider.once('connect')
                     *        clearInterval 500ms   reconnect   .
                     * 3) 2) interval  ,   else    error  ,
                     *    emit error  .
                     * 4) (optional) callback     .
                     */
                    // unsubscribe, but keep listeners
                    _this.options.requestManager.removeSubscription(_this.id);

                    // re-subscribe, if connection fails
                    if(_this.options.requestManager.provider.once) {
                        _this._reconnectIntervalId = setInterval(function () {
                            // TODO check if that makes sense!
                            if (_this.options.requestManager.provider.reconnect) {
                                _this.options.requestManager.provider.reconnect();
                            }
                        }, 500);

                        _this.options.requestManager.provider.once('connect', function () {
                            clearInterval(_this._reconnectIntervalId);
                            _this.subscribe(_this.callback);
                        });
                    }
                    _this.emit('error', err);

                     // call the callback, last so that unsubscribe there won't affect the emit above
                     if (_.isFunction(_this.callback)) {
                        _this.callback(err, null, _this);
                    }
                }
            });
        } else if (_.isFunction(_this.callback)) {
          /**
           * case 2)  -    , _this.callback   
           * -   error emit.
           */
            _this.callback(err, null, _this);
            _this.emit('error', err);
        } else {
            /**
             * case 3)  -    , _this.callback    
             * -  error emit.
             */
            // emit the event even if no callback was provided
            _this.emit('error', err);
        }
    });

    /**
     *    subscription instance  cancel   instance 
     *    return this .
     */
    // return an object to cancel the subscription
    return this;
};

module.exports = Subscription;
