var Jsonrpc = require('./jsonrpc');
var errors = require('../../caver-core-helpers').errors;

/**
 * requestManager parameter  bind  .
 *  )
 * web3-core `index.js`
 * pkg.BatchRequest = requestManager.BatchManager.bind(null, pkg._requestManager);
 *   this.requestManager   request manager    . (ex: sendBatch)
 * batch  request 'requests'   'add'   
 * 'execute'   'requests'  .sendBatch parameter ,
 *    'requests'     .
 */
var Batch = function (requestManager) {
    this.requestManager = requestManager;
    this.requests = [];
};

/**
 * request 'requests'  .
 */
Batch.prototype.add = function (request) {
    this.requests.push(request);
};

/**
 * 'requests'  requestManager sendBatch parameter ,
 *    'requests'     .
 */
Batch.prototype.execute = function () {
    var requests = this.requests;
    this.requestManager.sendBatch(requests, function (err, results) {
        /**
         *    results ,       .
         */
        results = results || [];
        /**
         * 'requests'    ,  index  results[index]
         *      .
         *  ,  requests       ,
         *   requestManager sendBatch  'requests'  parameter
         *       results  'requests'   
         *   .  requests index  .
         */
        requests.map(function (request, index) {
            return results[index] || {};
        }).forEach(function (result, index) {
          /**
           * requests[index] callback  
           * 1) result.error   -  ErrorResponse .
           * 2) result valid json response object  - valid  InvalidResponse .
           * 3) 1) 2)  , requests[index] format  , formatting ,
           *    parameter .
           */
            if (requests[index].callback) {

                if (result && result.error) {
                    return requests[index].callback(errors.ErrorResponse(result));
                }

                if (!Jsonrpc.isValidResponse(result)) {
                    return requests[index].callback(errors.InvalidResponse(result));
                }

                requests[index].callback(null, (requests[index].format ? requests[index].format(result.result) : result.result));
            }
        });
    });
};

module.exports = Batch;
